
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  /* color: #2c3e50; */
  margin-top: 60px;
}

p {
  font-size: 10vw;
  text-align: center;
  margin: 0;
}
button {
  padding-bottom: 10vw;
  margin: auto;
  font-size: 10vw;
  width: 70vw;
  height: 20vw;
  text-align: center;
  line-height: 20vw;
  border-radius: 5vw;
  border: solid transparent;
}
.animationImg {
  animation: myfirst 0.5s;
  -moz-animation: myfirst 0.5s; /* Firefox */
  -webkit-animation: myfirst 0.5s; /* Safari and Chrome */
  -o-animation: myfirst 0.5s; /* Opera */
  animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  -o-animation-iteration-count: infinite;
}
@keyframes myfirst {
0% {
    top: 0;
}
10% {
    top: -1vw;
}
20% {
    top: -2vw;
}
30% {
    top: -3vw;
}
40% {
    top: -2vw;
}
50% {
    top: -1vw;
}
60% {
    top: 0;
}
100% {
    top: 0;
}
}
@-webkit-keyframes myfirst {
0% {
    top: 0;
}
10% {
    top: -1vw;
}
20% {
    top: -5vw;
}
30% {
    top: -10vw;
}
40% {
    top: -5vw;
}
50% {
    top: -1vw;
}
60% {
    top: 0;
}
100% {
    top: 0;
}
}

.container {
  position: relative;
  width: 64vw;
  height: 64vw;
  margin: auto;
  /* background-color: cadetblue; */
  /* background-image: url("../assets/logo.png"); */
}
.card {
  border-radius: 10%;
  border: solid transparent;
  background-repeat: no-repeat;
  background-size: cover;
  border: none;
  position: absolute;
  width: 16vw;
  height: 16vw;
}
.layer1 {
  z-index: 1;
}
.layer2 {
  z-index: 2;
}
.layer3 {
  z-index: 3;
}
.layer4 {
  z-index: 4;
}
.layer5 {
  z-index: 5;
}

